import Cesium from "cesium/Cesium";
export default {
  methods: {
    addCylinederEntity(viewer, params) {

      return viewer.entities.add({ //Cylinder
        name: 'Green cylinder with black outline',
        position: Cesium.Cartesian3.fromDegrees(params.lng, params.lat, params.alt),
        cylinder: {
          length: params.length,
          topRadius: params.radius || 100, //
          bottomRadius: params.radius || 100, //    
          material: Cesium.Color[params.color].withAlpha(0.5) //
          // outline: true, //
          // outlineColor: Cesium.Color.DARK_GREEN //
        }
      });
    }
  }
}